import React from 'react'
import Link from 'gatsby-link'

import Logo from '../assets/img/logo.png'

const IndexPage = () => (
  <div>
    <section className="nkc-banner">
      <div className="container clearfix">
        <div className="col-lg-large col-md-large col-sm-full">
          <hrgroup>
            <h1 className="nkc-page-title"><span className="nkc-bg-hightlight">Welcome to</span></h1>
            <h1 className="nkc-page-title"><span className="nkc-bg-hightlight">North Kamrup College</span></h1>
            <h3 className="hidden-sm"><span className="nkc-bg-hightlight"><span className="fa fa-fw fa-certificate"></span> Accredited with B by National Assessment and Accreditation Council</span></h3>
          </hrgroup>
        </div>
      </div>
    </section>
    <main className="nkc-container nkc-container--landing-page clearfix">
      <div className="container clearfix">
        <div className="col-lg-large col-md-large col-sm-full">
          <div className="nkc-intro">
            <div className="nkc-intro__overlay"></div>
            <div className="nkc-intro__inner">
              <h1><span style={{ padding: '13px 34px', backgroundColor: '#efefef' }}>Photo Gallery<span></span></span></h1>
              <p><span style={{ padding: '13px 34px', backgroundColor: '#efefef' }}><Link to="/">Visit photo gallery &rarr;</Link></span></p>
            </div>
          </div>
        </div>
        <div className="col-lg-medium col-md-medium col-sm-full">
          <div className="nkc-highlight">
            <h2 className="text-uppercase"><span className="fa fa-fw fa-bookmark-o"></span> Highlights</h2>
            <img src={Logo} style={{ width: '55px', float: 'right' }} />
            <h1>North Kamrup College</h1>
            <p>Mon – Sat, 9AM – 4PM<br />Sun, Closed</p>
            <p>Establised 17 August 1979<br />N.A.A.C. Accredited 2017<br />Recognition by U.G.C. 18 May 2007</p>
            <p>680 Students<br />46 Teaching &amp; Non teaching staff</p>
          </div>
        </div>
      </div>
      <div className="container clearfix">
        <div className="col-lg-medium col-md-medium col-sm-full">
          <div className="nkc-principal-message">
            <h2 className="text-uppercase"><span className="fa fa-fw fa-star-o"></span> Welcome</h2>
            <h3>Dr. Surajit Barman<br />Principal, North Kamrup College</h3>
            <p>It gives me an immense pleasure to welcome you to the official website of North Kamrup College and I would like to thank you for taking the time to navigate its content.</p>
            <p>North Kamrup College, established in 1979 under the pioneering leadership of some visionaries of Baghmara area of Barpeta is a prominent seat of higher learning in Assam.</p>
            <p><Link to="/academics/#principal">Continue reading &rarr;</Link></p>
          </div>
        </div>
        <div className="col-lg-large col-md-large col-sm-full clearfix">
          <div className="nkc-welcome">
            <div className="nkc-welcome__inner">
              <h2 className="visible-sm text-uppercase"><span className="fa fa-fw fa-university"></span> The College</h2>
              <p>One of the premier institutions of higher education— North Kamrup College, Baghmara, established on the 17th August, 1979 is situated under the traditionally enlightened district of Assam, Barpeta. It is 12 Kms North from the head quarter of Bajali sub-division.</p>
              <p><Link to="/the-college">Read more &rarr;</Link></p>
            </div>
          </div>
          <Link to="/academics/#courses-offered"><span className="nkc-courses-offered">Courses offered &rarr;</span></Link>
        </div>
      </div>
    </main>
    <div className="spacer"></div>
  </div>
)

export default IndexPage



